import api from '~api'
import config from '~api-config'

let wlList = (params) => api.post('/base/logistics/list.action',config.api,params);//
let addWl= (params) => api.post('/base/logistics/create.action',config.api,params);//
let editWl= (params) => api.post('/base/logistics/edit.action',config.api,params);//
let updateWl= (params) => api.post('/base/logistics/update.action',config.api,params);//
let deleteWl= (params) => api.post('/base/logistics/delete.action',config.api,params);//
let addrList = (params) => api.post('/base/shippingaddress/insert/list.action',config.api,params);//()
let editAddrList = (params) => api.post('/base/shippingaddress/list.action',config.api,params);//
let addAddr = (params) => api.post('/base/shippingaddress/create.action',config.api,params);//
let deleteAddr = (params) => api.post('/base/shippingaddress/delete.action',config.api,params);//
let editAddr = (params) => api.post('/base/shippingaddress/edit.action',config.api,params);//
let updateAddr = (params) => api.post('/base/shippingaddress/update.action',config.api,params);//

export default {
    wlList,
    addWl,
    editWl,
    updateWl,
    deleteWl,
    addrList,
    editAddrList,
    addAddr,
    deleteAddr,
    editAddr,
    updateAddr
}



